'placeholder': [{ type: Input },],
'noResultsText': [{ type: Input },],
'searchIcon': [{ type: Input },],
'value': [{ type: Input },],
'valueChange': [{ type: Output },],
'lookup': [{ type: Input },],
'field': [{ type: Input },],
'setPick': [{ type: Input, args: ['pick', ] },],
